import moment from "moment";

export function UIFormatDateVNComponent(props: any) {
    let dateStr = "";
    if (props.value !== undefined && props.value !== '' && props.value !== null) {
        dateStr = moment(props.value).format("DD/MM/YYYY");
    } else {
        dateStr = "";
    }

    return (
        <span>{dateStr}</span>
    );
}

export function UIFormatDateTimeVNComponent(props: any) {
    let dateStr = "";
    if (props.value !== undefined && props.value !== '' && props.value !== null) {
        dateStr = moment(props.value).format("DD/MM/YYYY HH:mm:ss");
    } else {
        dateStr = "";
    }

    return (
        <span>{dateStr}</span>
    );
}